#ticket-card {
  min-width: 290px;
  max-width: 600px;
  padding: 2rem;
  -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  margin: 1rem 1rem 1rem 1rem;
  height: 40rem;
  background: black;
  color: white;
}
.ticket-img {
  width: 100%;
}
.card-body {
  height: 20rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.card-body > a {
  position: absolute;
  bottom: 0;
  left: 0;
}



